






















































import {Vue, Component, Prop} from 'vue-property-decorator';
import userSvc from '@/services/user';
@Component
export default class LoginStep1 extends Vue {
  private formData: any = {
    username: 'admin',
    password: 'admin',
    verifycode: '123456',
  };
  private checked: boolean = false;
  private logining: boolean = false;
  private captchaUrl: string = '';
  private rules: any = {};

  private mounted() {
    this.refreshToken();
  }
  private async loginSys() {
    // console.log('login...');
    try {
      this.logining = true;
      const payload = {
        username: this.formData.username,
        password: this.formData.password,
        verifycode: this.formData.verifycode,
      };
      await userSvc.login(payload);
      this.$router.push('/home');
    } catch (err) {
      console.log(err);
    } finally {
      this.logining = false;
    }
  }
  private refreshToken() {
    // this.captchaUrl = '/api/v1/captcha?time=' + Date.now();
    this.captchaUrl =
      'https://cloud.githubusercontent.com/assets/5518/22151427/e4939d92-df58-11e6-9754-4a46a86acea8.gif';
  }
}
